<template>
  <b-modal
    v-if="isShow"
    ref="modalCompanies"
    id="modalCompanies"
    title="Danh sách công ty"
    no-close-on-backdrop
    no-fade
    size="lg"
    hide-footer
  >
    <div class="row mb-1">
      <div class="col-12">
        <div class="input-group mb-3">
          <input
            type="text"
            placeholder="Nhập mã số thuế hoặc tên doanh nghiệp để tìm kiếm...."
            class="form-control"
            v-model="keyword"
            @keyup.enter="search()"
          />
          <button
            type="button"
            id="button-addon2"
            class="btn btn-primary-soft"
            @click="search()"
          >
            <i class="fa-solid fa-magnifying-glass"></i> Tìm kiếm
          </button>
        </div>
      </div>
    </div>
    <div class="card border-0">
      <div class="card-body p-0 overflow-auto" style="height: 400px">
        <table class="table table-dark-gray align-middle p-4 mb-0 table-hover">
          <thead class="table-dark">
            <tr>
              <th class="rounded-start">#</th>
              <th width="65%">Tên công ty</th>
              <th>Mã số thuế</th>
              <th width="10" class="rounded-end"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in companies" :key="item.company_id">
              <td>{{ index + 1 }}</td>
              <td>
                <span class="text-primary hand">{{ item.company_name }}</span>
              </td>
              <td>{{ item.company_taxcode }}</td>
              <td>
                <button
                  title="Chọn công ty"
                  class="btn btn-primary-soft btn-xs"
                  @click="choose(item)"
                >
                  <i class="fa-thin fa-circle-arrow-right"></i>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
        <NotFound v-if="companies.length == 0"></NotFound>
      </div>
    </div>
  </b-modal>
</template>

<script>
import pagination from "../extends/Paginate";
import NotFound from "./NotFound.vue";
export default {
  components: {
    pagination,
    NotFound,
  },
  data() {
    return {
      isShow: true,
      companies: [],
      keyword: "",
      pagination: {
        total: 0,
        per_page: 2,
        from: 1,
        to: 0,
        current_page: 1,
      },
      debounceTimeout: null,
    };
  },
  created() {},
  beforeMount() {
    //this.getInitialUsers();
    //this.getCompanies(this.pagination.current_page)
  },
  mounted() {
    //this.getNextUser();
    this.getNextCompanies(this.pagination.current_page);
  },
  methods: {
    search() {
      this.getCompanies(this.pagination.current_page);
    },
    show() {
      this.isShow = true;
      this.$bvModal.show("modalCompanies");
      this.getCompanies();
    },
    choose(item) {
      this.$emit("chooseCompany", item);
      this.$toast.success("Thêm công ty thành công");
      this.$bvModal.hide("modalCompanies");
    },
    async getNextCompanies(page) {},
    getCompanies(page) {
      this.$econtract.showLoading();
      
      if (this.keyword != "") page = 1;
      
      clearTimeout(this.debounceTimeout); // Xóa timeout cũ khi người dùng tiếp tục nhập liệu
      this.debounceTimeout = setTimeout(async () => {
        await axios
          .post(`/companies/searchScroll`, {
            pageSize: 20,
            page: page,
            name: this.keyword,
          })
          .then((response) => {
            this.companies = response.data.data;
            this.$econtract.hideLoading();
          });
      }, 300);
    },
  },
};
</script>
