<template>
  <b-modal
    v-if="isShow"
    id="modalCustomers"
    title="Danh sách đầu mối liên hệ"
    no-fade
    size="md"
    hide-footer
  >
    <div class="row mb-1">
      <div class="col-12">
        <div class="input-group mb-3">
          <input
            type="text"
            placeholder="Nhập tên đầu mối liên hệ để tìm kiếm...."
            class="form-control"
            v-model="keyword"
            @keyup.enter="search()"
          />
          <button
            type="button"
            id="button-addon2"
            class="btn btn-primary-soft"
            @click="search()"
          >
            <i class="fa-solid fa-magnifying-glass"></i> Tìm kiếm
          </button>
        </div>
      </div>
    </div>
    <div class="card border-0">
      <div class="card-body p-0 overflow-auto" style="height: 400px">
        <table class="table table-dark-gray align-middle p-4 mb-0 table-hover">
          <thead class="table-dark">
            <tr>
              <th class="rounded-start">Tên đối tác</th>
              <th>Số điện thoại</th>
              <th width="10" class="rounded-end"></th>
            </tr>
          </thead>
          <tbody>
            <tr :title="item.customer_name" v-for="item in customers" :key="item.customer_id" class="hand">
              <td>
                <span class="text-primary"  :title="item.customer_name">{{ item.customer_name }}</span>
              </td>
              <td >{{ item.customer_phone }}</td>
              <td>
                <button
                  title="Chọn công ty"
                  class="btn btn-primary-soft btn-xs"
                  @click="choose(item)"
                >
                  <i class="fa-thin fa-circle-arrow-right"></i>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
        <NotFound v-if="customers.length == 0"></NotFound>
      </div>
    </div>
  </b-modal>
</template>

<script>
import NotFound from './NotFound.vue';
export default {
  components:{
    NotFound
  },
  data() {
    return {
      isShow: true,
      customers: [],
      keyword: "",
      pagination: {
        total: 0,
        per_page: 2,
        from: 1,
        to: 0,
        current_page: 1,
      },
    };
  },
  created() {},
  methods: {
    search() {
      this.getCustomers(this.pagination.current_page);
    },
    show() {
      this.isShow = true;
      this.$bvModal.show("modalCustomers");
      this.getCustomers();
    },
    close() {
      this.$bvModal.hide("modalCustomers");
    },
    choose(item) {
      this.$emit("chooseCustomer", item);
      this.$toast.success("Thêm người liên hệ thành công");
      this.$bvModal.hide("modalCustomers");
    },
    async getCustomers(page) {
      this.$econtract.showLoading();
      if (this.keyword != "") page = 1;
      await axios
        .post(`/customers/searchScroll`, {
          pageSize: 20,
          page: page,
          name: this.keyword,
        })
        .then((response) => {
          this.customers = response.data.data;
          this.$econtract.hideLoading();
        });
    },
  },
};
</script>
