<template>
  <tr v-if="!detail.hasOwnProperty('isDelete')">
    <td>{{ index + 1 }}</td>
    <td>
      <textarea
        type="text"
        v-model="detail.detail_content"
        class="form-control"
        rows="1"
        @click="autoComplete.isShow = true"
        data-bs-toggle="dropdown"
      />
      <ul class="dropdown-menu bg-blur">
        <li v-for="item in filterItem" :key="item" @click="setItem(item)">
          <a class="dropdown-item hand" :title="item">{{ item }}</a>
        </li>
        <li v-if="filterItem.length == 0">
          <div class="text-danger text-center">Không có dữ liệu</div>
        </li>
      </ul>
    </td>
    <td>
      <input type="text" v-model="detail.detail_unit" class="form-control" />
    </td>
    <td>
      <input type="text" v-model="detail.detail_quantity" class="form-control" />
    </td>
    <td>
      <money
        class="form-control"
        v-bind="money"
        v-model.lazy="detail.detail_product_price"
      />
    </td>
    <td>
      <input type="text" v-model="priceAfterTax" class="form-control" />
    </td>

    <td>
      <button class="btn btn-primary-soft btn-sm" @click="sendDetail(detail, index)">
        <i class="fa-thin fa-pen-to-square"></i>
      </button>
      <button
        class="btn btn-danger-soft btn-sm ms-1"
        @click="deleteDetail(detail, index)"
      >
        <i class="fa-thin fa-trash-can"></i>
      </button>
    </td>
  </tr>
</template>

<script>
import { Money } from "v-money";

export default {
  components: {
    Money,
  },
  props: {
    detail: Object,
    index: Number,
  },
  data() {
    return {
      autoComplete: {
        items: [
          "Dịch vụ chữ ký số công cộng VNPT-CA ({key})",
          "HD{key} ({key} hóa đơn)",
          "Chứng từ thuế thu nhập cá nhân HD{key} ({key} chứng từ)",
          "Dịch vụ kê khai BHXH VNPT-BHXH ({key})",
          "Gia hạn dịch vụ giám sát thành trình VNPT-TRACKING ({key})",
          "Gói cước dịch vụ VNPT-CA trên sim Vinaphone ({key})",
          "Dịch vụ chữ ký số công cộng VNPT-CA tích hợp VNPT-BHXH ({key})",
        ],
        isShow: false,
      },
      isDelete: false,
      total: 0,
      priceAfterTax: 0,
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "",
        suffix: " ",
        precision: 4,
        masked: false,
      },
    };
  },
  watch: {
    priceAfterTax: {
      handler(value) {
        this.detail.detail_product_price = parseFloat(
          this.priceAfterTax / 1.1 / this.detail.detail_quantity
        ).toFixed(4);
      },
    },
  },
  computed: {
    filterItem() {
      this.autoComplete.isShow = true;
      return this.autoComplete.items.filter((x) => {
        if (this.detail.detail_content)
          return x.toLowerCase().indexOf(this.detail.detail_content.toLowerCase()) !== -1;
      });
    },
  },
  created() {},
  methods: {
    setItem(item) {
      let productName = this.detail.product.text;
     
      // typeof this.detail.product.product_name == undefined
      //   ? this.detail.product.text
      //   : this.detail.product.product_name;

      this.detail.detail_content = item.replace(/\{key\}/g, productName);
      this.autoComplete.isShow = false;
    },
    reverse() {
      this.detail.detail_product_price = parseFloat(this.total / 1.1).toFixed(4);
    },
    deleteDetail(detail, index) {
      this.$emit("deleteDetail", detail, index);
    },
    sendDetail(detail, index) {
      this.$emit("sendDetail", detail, index);
    },
  },
};
</script>
